body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Dropdown-section {
    background-color: rgb(139, 192, 228);;
}

.Dropdown-class-type, .Dropdown-class-rname, .Dropdown-class-day, .Dropdown-class-time, .Dropdown-class-disabled {
    border-radius: 4px;
    background-color: #FCFCFC;
    padding-left: 10px;
    cursor: pointer;
}

.Dropdown-class-disabled {
    border-radius: 4px;
    background-color: #DDDDDD;
    color: #808080;
}

.Dropdown-header-type, .Dropdown-header-rname, .Dropdown-header-day, .Dropdown-header-time {
    padding-left: 10px;
    width: 25%;
    font-weight: bold;
    text-align: left;
    color:#FCFCFC;
}

.Dropdown-header-rname {
    width: 60%;
}

.Dropdown-header-day {
    width: 30%;
}

.Dropdown-header-time {
    width: 30%;
}

.Dropdown-menu-type, .Dropdown-menu-name, .Dropdown-menu-day, .Dropdown-menu-time {
    border-radius: 4px;
    position: absolute;
    background-color: white;
    color: #404040;
    text-align: left;

    border: 1px solid #ccc;
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.06);
    box-sizing: border-box;

    max-height: 200px;
    overflow-y: auto;
    width: 20%;
    cursor: pointer;
}

.Dropdown-menu-name {
    width: 50%;
}

.Dropdown-menu-day {
    width: 25%;
}

.Dropdown-menu-time {
    width: 25%;
}

.Dropdown-control {
    color: #404040;
    font-weight: bold;
    text-align: left;
    padding-top: 6px;
    padding-bottom: 6px;
}

.Dropdown-option {
    box-sizing: border-box;
    color: rgba(51, 51, 51, 0.8);
    cursor: pointer;
    display: block;
    padding: 8px 10px;
  }

.Dropdown-option:hover {
    background-color: #eeeeee;
    color: #333;
  }

.Dropdown-icon {
    position: relative;
    float:right;
    padding-right: 12px;
    bottom: 20px;
}

.filter-results {
    width: 80%;
    margin: 0 auto 0 auto;
    padding-top: 1%;
    padding-bottom: 1%;
    background-color: #EEEEEE;

}
table {
    border-collapse: collapse;
    width: 80%;
    margin: 3vh auto 0vh auto;
}

td, th {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: 'left';
  }
  
tr:nth-child(even){
    background-color: #f2f2f2;
}

.tr-entries:hover {
    background-color: #ddd;
}

th {
    padding: 12px;
    background-color: rgb(139, 192, 228);
    color: white;
    text-align: 'left';
}

.header-restaurant {
    width: 29%;
}

.header-hours {
    width: 55%;
}

.header-support {
    width: 20%;
}

.button {
    display: inline-block;
    border-radius: 4px;
    background-color: #F4511E;
    border: none;
    color: #FFFFFF;
    text-align: center;
    padding: 5%;
    cursor: pointer;
    opacity: 0;
}

.button:disabled {
    background-color: #808080;
    cursor: default;
}

tr:hover .button {
    display: inline-block;
    opacity: 1;
}


  
.modal-section {
    border-radius: 4px;
    padding-bottom: 10px;
}

.modal-header {
    padding-top: 2px;
    padding-bottom: 5px;
    background-color: #DDDDDD;
    font-weight: bold;
    text-align: center;
}

.modal-content {
    padding: 20px 0px 10px 0px;
    text-align: center;
}

.modal-button0, .modal-button1, .modal-button2,
.modal-button-cancel, .modal-button-ok, .modal-button-share {
    min-width: 20%;
    min-height: 35px;
    margin: 5px 10px 0px 10px;
    border-radius: 4px;
    border: none;
    padding: 5px 10px;
    color: #000000;
    font-weight: bold;
    text-align: center;
    cursor: pointer;
}

.modal-button0 {
    background-color: #ffdfba;
}

.modal-button1 {
    background-color: #baffc9;
}

.modal-button2 {
    background-color: #ffffba;
}

.modal-button-cancel {
    background-color: #F4511E;
    color: #FFFFFF;
}

.modal-button-crossicon {
    padding-right: 5px;
    float: right;
    cursor: pointer;
}

.modal-button-ok {
    background-color: rgb(30, 123, 244);
    color: #FFFFFF;
}

.modal-button-share {
    background-color: rgb(38, 196, 64);
    color: #FFFFFF;
}

.modal-input {
    margin-bottom: 8px;
    float: center;
    text-align: center;

}
.collection-header {
    text-align: left;
    width: 80%;
}

.collection-header-loggedout {
    background-color: #38cf83;
    text-align: center;
    width: 50%;
}
.list-button0, .list-button1, .list-button2, .list-button-shared {
    width: 80%;
    margin: 0 auto 0 auto;
    border-radius: 4px;
    background-color: #EEEEEE;
    border: none;
    padding-top: 10px;
    padding-bottom: 10px;
    cursor: pointer;
}

.list-button0 {
    background-color: #ffdfba;
}

.list-button1 {
    background-color: #baffc9;
}

.list-button2 {
    background-color: #ffffba;
}

.list-button-shared {
    background-color: #ffffba;
}

button:disabled {
    background-color: #808080;
    cursor: default;
}
.login-header {
    width: 50%;
    margin: 0 0 0 0;
    background-color: #FFFFFF;
    border: none;
    text-align: left;
    color:#606060;
}

.login-button {
    border-radius: 4px;
    background-color: #2240e6;
    border: none;
    padding: 10px 20px 10px 20px;
    text-align: left;
    color: #FCFCFC;
    cursor: pointer;
}

.login-userlabel {
    width: 50%;
    margin: 0 0 0 0;
    background-color: #FFFFFF;
    border: none;
    text-align: right;
    color:#606060;
}
.app {
  text-align: center;
}

.app-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.app-header {
  background-color: #282c34;
  min-height: 13vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(12px + 2vmin);
  line-height: 1vh;
  color: white;
}

.subtitle {
  line-height: 0vh;
  font-size: 2vmin;
  padding-bottom: 4vh;
}

.app-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

